.block .accordion{background-color:#eee;color:#444;cursor:pointer;padding:18px;width:100%;text-align:left;border:none;outline:none;transition:.3s;border-radius:1px}.block .panel{padding:0 18px;background-color:#fff;transition:max-height .5s ease-out;line-height:1.4;overflow:hidden}.block .panel-hiden{max-height:0}.block .panel-active{max-height:280px}@media(max-width: 700px){.block .panel-active{max-height:auto}}.active,.accordion:hover{background-color:#ccc}.accordion:after{content:"➕";font-size:13px;color:#86d9f5;float:right;margin-left:5px}.active:after{content:"➖"}